.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 60px;
  z-index: 10;
}

.active {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 60px;
  z-index: 10;
  background-color: #000;
  transition: 0.8s;
}

.textMovie {
  color: red;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.2rem;
  float: left;
  margin-top: 16px;
  margin-left: 30px;
}

.searchIcon {
  float: right;
  margin-top: 13px;
  margin-right: 30px;
}
.navbar a:hover {
  cursor: pointer;
}

.content {
  display: flex;
}

.navBarImg {
  position: relative;
  width: 100%;
  height: 450px;
  object-fit: cover;
}

.information {
  position: absolute;
  top: 126px;
  left: 30px;
  max-width: 400px;
  max-height: 300px;
  color: white;
  font-weight: 600;
  line-height: 1.6;
  font-size: 1.2rem;
  overflow: hidden;
}

.information h1 {
  font-size: 2.4rem;
}

.information p {
  display: block; /* Fallback trong trường hợp trình duyệt không hỗ trợ display dạng box */
  display: -webkit-box;
  font-size: 15px;
  line-height: 24px; /* Fallback  */
  max-height: 74px; /* Fallback số dòng bạn muốn giới hạn */
  -webkit-line-clamp: 3; /* Số dòng bạn muốn giới hạn */
  -webkit-box-orient: vertical;
  visibility: visible;
  text-overflow: ellipsis;
  overflow: hidden;
}

.listBtn button {
  width: 80px;
  height: 30px;
  border: none;
  color: white;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}
.listBtn button:hover {
  cursor: pointer;
  opacity: 0.8;
}

.listBtn button:first-child {
  margin-right: 20px;
}

.FilmsLoading {
  text-align: center;
  color: white;
  font-size: 1.3rem;
  margin-top: 20px;
}

.FilmsError {
  text-align: center;
  color: red;
  font-size: 1.3rem;
  margin-top: 20px;
}
